          </h2>
          <div>
            <p class="font-light text-teal-900 opacity-50 text-center mb-1">
              shadows
            </p>
            <verte
              class="verte-input"
              :showHistory="null"
              menuPosition="top"
              model="hex"
              value="#234E52"
              v-model="darksInput"
            >
              <svg
                width="84"
                height="60"
                viewBox="0 0 84 60"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <rect width="84" height="60" rx="10" />
              </svg>
            </verte>
            <p class="font-light text-teal-900 opacity-75 text-center mt-2">
              {{ darksInput }}
            </p>
          </div>
          <div class="ml-16">
            <p class="font-light text-teal-900 opacity-50 text-center mb-1">
              highlights
            </p>
            <verte
