            >drag your image here or click to upload</label
          >
          <img
            class="image__preview"
            :style="{ filter: `url(#${filterName})` }"
            :src="imagePath"
          />
          <input
            class="image__file-input"
            type="file"
            id="file"
            name="file"
            @change="onFileSelected"
          />
        </div>
      </div>
